<template>
  <div class="shopWrap">
    <div class="sort-box">
      <a href="#" @click="lowPrice">낮은가격순</a>
      <a href="#" @click="highPrice">높은가격순</a>
    </div>
    <div class="items-box">
      <a @click="lookclock">CLOCK</a>
      <a @click="lookGoods">GOODS</a>
      <a @click="lookBooks">BOOKS</a>
      <!-- <form><input type="serach" placeholder="Seach" /></form> -->
    
    </div>
    <div class="display" :style="`opacity : ${fade}`">
      <div class="display-item" v-for="(a, i) in moroDataShow" :key="i">
        <div class="show-item">
          <img @click="$router.push(`/detail/${moroDataShow[i].id}`)"
            :src="moroDataShow[i].mainImage"
            alt=""
            :style="{ 'max-width': '100%', height: 'auto' }"
          />
          <img 
            :src="moroDataShow[i].serveImage"
            alt=""
            :style="{ 'max-width': '100%', height: 'auto' }"
          />
        </div>
        <div class="cont-box">
          <a href="">{{ moroDataShow[i].name }}</a>
          <p>{{ (moroDataShow[i].price).toLocaleString() }}원</p>
          <!-- <p>{{ moroData[i].Dprice }}원</p> -->
        </div>
      </div>
    </div>
  </div>

</template>

<script>
// import moroData from './assets/moroData.js'
import moroData from '../assets/moroData'

export default {
  name: 'shoP',
  data() {
    return {
      hover: 0,
      moroData : moroData,
      moroDataShow : moroData,
      fade: 1,
      // clock: [...moroData],
      Sorts: [
        "낮은가격순",
        "높은가격순",
      ],

    };
  },
  methods:{
    highPrice(){
      this.moroDataShow.sort(function(a, b){
        return b.price - a.price;
      });
    },

    lowPrice(){
      this.moroDataShow.sort(function(a, b){
        return a.price - b.price;
      });
    },
    lookclock(){
      this.moroDataShow = this.moroData.filter(moroData => moroData.item === 'clock');
      this.fade = 0
      setTimeout(() => {
        this.fade = 1
      }, 500);

    }
      ,
  lookGoods(){
    // this.clock= this.moroData.filter(moroData => moroData.item === 'goods');
    this.moroDataShow = this.moroData.filter(moroData => moroData.item === 'goods');
    this.fade = 0
      setTimeout(() => {
        this.fade = 1
      }, 500);
  },
lookBooks(){
    this.moroDataShow = this.moroData.filter(moroData => moroData.item === 'book');
    this.fade = 0
      setTimeout(() => {
        this.fade = 1
      }, 500);
  },
  },
  
  components: {
  },
  }
</script>

