<template>
  <div class="wrap"><!-- wrap---------------------------------->


  <div class="slider">
    <div class="slidcover" @mousemove="testmousemove" @mousedown="testmousedown" @mouseup="testmouseup"></div>
    <div class="slidwrap" :style="`margin-left:${moveSlide * -1 /8}px ; transform: translateX(${imgchange}00vw);`"><img v-for="(a,i) in slideImg" :key="i" :src="slideImg[i]" alt=""></div>
    <!-- <div class="slidwrap" :style="`transform: translateX(calc(${imgchange}00vw + ${moveSlide * -1 /8}px ) );`"><img v-for="(a,i) in slideImg" :key="i" :src="slideImg[i]" alt=""></div> -->
  </div>


  </div><!-- wrap---------------------------------->
  </template>
  
  <script>
  
  
  export default {
    name: 'App',
    data() {
      return {
        gnb:['LOGIN', 'JOIN', 'MYPAGE', 'BOARD'],
        subMenu:['ABOUT', 'SHOP', 'PHOTO', 'VIDEO', 'NOTICE', 'REVIEW', 'Q&A'],
        subMenu_Modal: false,
        slideImg: [
          'https://cdn.imweb.me/thumbnail/20230222/287c1b05ebc14.jpg',
          'https://cdn.imweb.me/thumbnail/20230222/6fbf3ad6ca25f.jpg',
        ],
        mousedown: 0,
        mouseup: 0,
        mousemove:0,
        moveSlide:0,
        slidclick : false,
        imgchange: 0,
      }
    },
    methods: {
      gnbClick(e){
        if(e.target.id == 'BOARD'){
          this.subMenu_Modal= true;
        }
      },
      testmousedown(e){
        this.mousedown = e.clientX;
        this.slidclick = true;

      },

      testmouseup(e){
        this.mouseup = e.clientX;
        if(this.moveSlide < 200){
          this.imgchange++
        }
        if(this.moveSlide > -200){
          this.imgchange--
        }
        this.mouseup = 0;
        this.mousedown = 0;
        this.mousemove = 0;
        this.slidclick = false;
        
      },

      testmousemove(e){
        if(this.slidclick){
          this.mousemove = e.clientX;
        }else{
          this.mousemove = 0;
        }
        
        this.moveSlide = this.mousedown - this.mousemove;
        

      },

    },
    mounted() {
      
    },
    components: {
    }
  }
  </script>
  
  <style>
  .wrap{width: 100%;position: relative;top: 0;}
  *{margin: 0;padding: 0; text-decoration: none; box-sizing: border-box;}
  .hd { background: #000; width: 100%;height: 4vw; display: flex; justify-content: space-between;align-items: center;}
  .gnb *{color: #fff; list-style: none; cursor: pointer; transition: all 0.3s;}
  .gnb *:hover{color: #000; text-shadow: -1px 0px #fff, 0px 1px #fff, 1px 0px #fff, 0px -1px #fff;}
  .menu {margin: 0.7vw; font-size: 1.7vw;}
  
  
  .subMenu_Modal{position: fixed;background: #0000004b;width: 100vw;height: 100vh;top: 0;left: 0;}
  .modalinner{width: 30%;position: absolute;top: 50%;left: 50%;transform: translate(-50%, -50%); text-align: center;font-weight: bolder;}
  .subMenu {font-size: 3.5vw;margin: 1.5vw; color: #fff;
    text-shadow: -1px 0px #000, 0px 1px #000, 1px 0px #000, 0px -1px #000; cursor: pointer;}
    .cancel{ width: 30px;height: 30px; color: #fff; transition: all 1s; cursor: pointer;  text-shadow: -1px 0px #000, 0px 1px #000, 1px 0px #000, 0px -1px #000; font-size: 2vw; position: absolute ; right: 0;}
    .cancel:hover{ color: #000; text-shadow: -1px 0px #fff, 0px 1px #fff, 1px 0px #fff, 0px -1px #fff;}
  .logo {
    width: 20%;
    height: 100%;  
    /* border: 0.2vw solid #fff; */
    position: relative;
    }
    .logo img{height: 90%;max-height: 90%; position: absolute;left: 50%;top: 50%; transform: translate(-50%, -50%);}

    .slider{width: 100%;height: 100vh; overflow: hidden;position: relative;}
    .slidcover{width: 100%;height: 100%;position: absolute; z-index: 100;}
    .slidwrap{width: 200%;height: 100%;display: flex; transition:transform  0.8s;}
    .slider div img{width: 100vw;max-width: 100vw;}
  </style>