<script>
import data from '../assets/product.js'
import inputs from '../assets/inputsave.js'


export default {
  name: 'App',
  data() {
    return {
      step:0,
      noticeClick:-1,
      showModal: false,
      title: '',
      product:data,
      inputs:inputs,
      tempValue: '',
      notice2Click : -1,
    }
  },
  methods: {
    noticeToggle(e){
      if(this.noticeClick != e.target.dataset.id){
        this.noticeClick = e.target.dataset.id;
        // this.product[e.target.dataset.id].imageText = "강아지"
        
      }else{
        this.noticeClick = -1;
        // this.product[e.target.dataset.id].imageText = "되나?"
        
      }
    },
        notice2Toggle(e){
      if(this.notice2Click != e.target.dataset.id){
        this.notice2Click = e.target.dataset.id;
      }else{
        this.notice2Click = -1;
      }
    },
    

    
      submit() {
      this.showModal = false
      let saveText={
        title : this.title,
        tempValue: this.tempValue,
      };
      this.inputs.unshift(saveText);
      console.log(saveText);
      
      this.title = '';
      this.tempValue = '';
    },
  },

  components: {
    
    
  }
}
</script>

